  display: flex;
  flex-direction: column;
  align-items: center;
  height: 83vh;
}

.links {
  display: flex;
  justify-content: center;
  align-items: center;
}

.link {
  display: block;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: $white;
  margin: 20px 5px 0;
}